.contact {

  display: flex;
  justify-content: center;
  flex-wrap: wrap;

}

.mycontact {
  display: flex ;
  width:45%;
   flex-wrap: wrap;
  margin-right:30px;

}
.contact_title {
  display: flex;
  justify-content: center;
  text-align: center;
  width: 100%;

}
.contact_title h1{
color: #4a2c79;
text-transform: uppercase;

}
.contact_information {
   display: flex;
   text-align: justify;
  justify-content: center;
  width: 100%;

}

.contact_requisites {
  display: flex;
  justify-content: center;
  width: 45%;
   flex-wrap: wrap;
  margin-left: 30px;

}
.contact_requisites_info {
  display: flex;
  justify-content: center;
  text-align: justify;
  width: 100%;

}
.contact_map{
	margin-left: 20px;
	margin-right: 20px;
	margin-bottom: 30px;
}


@media screen and (min-width: 290px) and (max-width: 800px) {

	.contact_requisites {
		width: 100% !important;
		margin-left: 0px !important;
	}
	.mycontact {
		width: 100% !important;
		margin-right: 0px !important;
	}


}
