.my_mobile_footer {
	display: none;
}


@media screen and (min-width: 290px) and (max-width: 500px) {
	.my_mobile_footer {
		display: block;
	}

	.all_line_box{
		display: none;
	}

	
		.footer {
			display: none;
		}



}



.footer_mob {
	margin-top: 30px;
  display:flex;
  width:100%;


  flex-wrap:wrap;
  justify-content:center;
}
.footer_divider {
  display:flex;
  width:80%;
  border:2px solid #4a2c79;
  height:0;
	margin-bottom: 10px;
  border-radius:5px

}
.footer_content {
  width:60%;
  display:flex;
	flex-wrap: wrap;
  justify-content:center;

}
.footer_contact {
	justify-content: center;
  display:flex;
  width: 250px;
  flex-wrap:wrap;

}
.footer_location {
	justify-content: center;
  display:flex;
  width: 250px;
}
.footer_time {
  display: flex;
  width: 250px;
}
.footer_tel{display: flex;
  width: 200px;

}
.footer_copyright {
  display:flex;
  width:100%;
  justify-content:center;
}
.copyright_footer {
  display:center;
  justify-content:center;
}

.footer_location {
	display: flex;
    justify-content: center;
    flex-flow: row;

}
.footer_icon_location {
	display: flex;
    justify-content: center;
    align-items: center;
}

.footer_email {
	display: flex;
    justify-content: center;
    flex-flow: row;
}

.footer_icon_email {
	display: flex;
    align-items: flex-end;
    justify-content: center;
}


.footer_time {
	display: flex;
    justify-content: center;
    flex-flow: row;

}

.footer_icon_time {
	display: flex;
align-items: center;
justify-content: center;
margin-right: 10px;

}

.footer_tel {
	display: flex;
	align-items: center;
    justify-content: center;
    flex-flow: row;

}


.footer_icon_tel {
	display: flex;
align-items: center;
justify-content: center;
margin-right: 10px;

}


.footer_copyright {
	display: flex;
	width: 80%;
	text-align: center;
	justify-content: center;
	margin-top: 15px;

}

.copyright_footer {
	width: 100%;
	display: flex;
	justify-content: center;
}
