.blog {
flex-flow: column;
  display: flex;
  width: 100%;
  flex-wrap: wrap;


}

.blog_title__box{
	display: flex;
	width: 100%;
	justify-content: center;
	height: 50px;

}
.blog__title {
	display: flex;
	width: 80%;
	justify-content: center;
	height: 50px;

}
.blog__title h1 {

	color: #4a2c79;
	    text-transform: uppercase;
}




.blog_card {
  display:flex;
  width: 100%;
  height: 250px;
  margin:20px;

}
.blog_card_info {
  flex-wrap:wrap;
  display:flex;
  width: 100%;
  height:250px;
  justify-content: center;


}
.blog_date_title {
   height:30px;
  display: flex;
  width: 100%;


  justify-content: flex-start;
}
.blog_date {
  background-color: #4a2c79;
  display:flex;
  width:150px;
   height:30px;

  justify-content:flex-start;
}
.blog_date p {
  text-align: center;
  color: white;
  padding-left:15px;
}

.blog_title {
  display:flex;
  width:100%;
  align-items: center;
  justify-content:center;
}
.blog_title h3 {
	color: #4a2c79;
	    text-transform: uppercase;
}


.blog_info {
  display:flex;
  width:100%;
  text-align: justify;

}

.blog_card_foto {
  display:flex;
  flex-wrap:wrap;
	width: 60%;
  justify-content:flex-end;

}

.blog_foto {
  display:flex;
  justify-content:center;
  height: 185px;
  width :290px;
  background-color: black;
}
.blog_button {
  display:flex;
  background-color: #4a2c79;
  width:290px;
  height: 40px;
  justify-content: center;

}

.blog_button a{ color: white;

}
