.news_box__wrapp{
	display: flex;
	justify-content: center;
	



}



.news {
	flex-flow: column;

  display: flex;
  width: 100%;
  flex-wrap: wrap;


}
.news_title__box{
	display: flex;
	width: 100%;
	justify-content: center;
	height: 50px;

}
.news__title {
	display: flex;
	width: 80%;
	justify-content: center;
	height: 50px;

}
.news__title h1 {

	color: #4a2c79;
	    text-transform: uppercase;
}



.news_card {
  display:flex;
  width: 100%;
  height: 250px;
  margin:20px;

}
.news_card_info {
  flex-wrap:wrap;
  display:flex;
  width: 100%;
  height:250px;
  justify-content: center;


}
.news_date_title {
   height:30px;
  display: flex;
  width: 100%;


  justify-content: flex-start;
}
.news_date {
  background-color: #4a2c79;
  display:flex;
  width:150px;
   height:30px;

  justify-content:flex-start;
}
.news_date p {
  text-align: center;
  color: white;
  padding-left:15px;
}

.news_title {
  display:flex;
  width:100%;
  align-items: center;
  justify-content:center;
}

.news_title h3 {
	color: #4a2c79;
	    text-transform: uppercase;
}

.news_info {
  display:flex;
  width:100%;
  text-align: justify;

}

.news_card_foto {
  display:flex;
  flex-wrap:wrap;
	width: 60%;
  justify-content:flex-end;

}

.news_foto {
  display:flex;
  justify-content:center;
  height: 185px;
  width :290px;
  background-color: black;
}
.news_button {
  display:flex;
  background-color: #4a2c79;
  width:290px;
  height: 40px;
  justify-content: center;

}

.news_button a{
	color: white;

}


@media screen and (min-width: 290px) and (max-width: 800px) {

	.left_menu_navigation {
		display: none;
	}


}
