.delivery {

  display: flex;
  justify-content: center;
  flex-wrap: wrap;
	margin-bottom: 40px;

}



.delivery_zone {
  flex-wrap:wrap;
	flex-flow: column;
  display:flex;
max-width: 1065px;
  justify-content:center;

}
.delivery_map {

  display: flex;
  height: 300px;
	width: 1065px;
  
	margin-bottom: 30px;
}

.mydelivery {
  display: flex ;
  width:45%;
  flex-wrap: wrap;
  margin-right:30px;

}

.delivery_title h1 {

	text-transform: uppercase;
	color: #4a2c79;
	font-size: 35px;


}



.delivery_title {
  display: flex;
  justify-content: center;
  text-align: center;
  width: 100%;
}
.delivery_info_price {
   display: flex;
	 flex-flow: column;
   text-align: justify;
  justify-content: center;
  width: 100%;

}

.delivery_condition {
  display: flex;
  justify-content: center;
  width: 45%;
   flex-wrap: wrap;
  margin-left: 30px;

}
.delivery_info {
  display: flex;
  justify-content: center;
  text-align: justify;
  width: 100%;

}





@media screen and (min-width: 290px) and (max-width: 800px) {

	.delivery_condition{
		width: 100% !important;
		margin-left: 0px !important;
	}
	.mydelivery{
		width: 100% !important;
		margin-right: 0px !important;
	}


}
