.service_elements__box {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	max-width: 1200px;
}

.background_box a {
	width: 100%;
	height: 250px;

}


.service_card {
	margin:10px;
}
.service_card_image {
	height:200px;
	width: 250px;
	z-index: 1;
	/*background-color: #4a2c79;*/
	position: relative;

}


@media (max-width: 576px) {
.service_card_image {
		height: 100px;
		width: 125px;
}
}

.service_card_image:hover {
	border: 2px solid #4a2c79;

}
.service_card_image:hover::after {
	opacity: 0;
	transition: 0.3s;

}

.service_card_image::after {
	position: absolute;
	content: '';
	opacity: 0.7;
	height: 100%;
	width: 100%;
	z-index: 3;
	background-color: #4a2c79;
	cursor: pointer;
}

.background_box p {
	position: absolute;
	bottom: 0;

	color			: white;
	font			: bold 18px/45px Helvetica, Sans-Serif;
	letter-spacing		: -1px;
	line-height: 1;
z-index: 15;
	padding: 10px;
}


.background_box p::after{
	position: absolute;
	bottom: 0;

	color			: white;
	font			: bold 18px/45px Helvetica, Sans-Serif;
	letter-spacing		: -1px;
	line-height: 1;
	z-index: 10;
	padding: 10px;
}


.background_box p::before {
	content: '';
	width: 5px;
	height: 26px;
	background-color: white;
	position: absolute;
	left: 2px;
	top: 30%;
}
