.price__wraper {

	display: flex;

	justify-content: center;
	flex-wrap: wrap;
	width: 100%;
}
.price__title {
	display: flex;
    justify-content: center;
    flex-wrap: wrap;
    text-transform: uppercase;
    text-align: center;
    width: 100%;
}
.price__title h1 {
	color: #4a2c79;
    margin-bottom: 20px;
    text-transform: uppercase;
}

.price__wrapp{
	display: flex;
	flex-flow: column;
	flex-wrap: wrap;
	justify-content: center;
	max-width: 1080px;
	margin-bottom: 100px;

}


.price__wrapp h1 {
	margin-top: 35px;
	margin-bottom: 5px;
	font-size: 25px;
	color: white;
	text-transform: uppercase;
	width: 100%;
	text-align: center;
	background-color: #49287a;
}
.first_h1{
	margin-top:1px !important;

}
.price__wrapp p{
	margin-bottom: 2px;
}
.price__wrapp tr {
	text-transform: none;
}
td {
	text-align: center;
	border: 3px solid #4a2c79;

}
.price__wrapp tr:hover{
	color:white;
}
