.dropdown_nav {
 width: 80%;
 text-align: center;
}
.dropdown_nav .viev_part {
    color: red;
    font-size: 18px;
    cursor: pointer;
    font-weight: lighter;
    position: relative;

}


.dropdown_box {
	position: absolute;
  display:flex;
	height: 250px;
	opacity: 0;
  visibility: hidden;
  width:1080px;
	margin-top: -25px;
  justify-content:center;

}


.menu_dropdown_box {
  display:flex;
  position: absolute;
    cursor: grab;
  visibility: hidden;
    opacity: 0;
  flex-wrap:wrap;
  justify-content:flex-end;
  width:100%;
	z-index: 10;
  height: 250px;

}
.dropdown_nav:hover .dropdown_box {
    visibility: visible;
    opacity: 1;
}
.dropdown_nav:hover .menu_dropdown_box {
    visibility: visible;
    opacity: 1;
}


.top_dropdown_box{
  display:flex ;
  justify-content:flex-start;
  width:783px;
  height:0;
  border-bottom: 30px solid #dbdbdb;
  border-left: 30px solid transparent;
}

.middle_dropdown_box{
  display:flex;
  width:1080px;
  height:220px;
  background-color: #dbdbdb;

}



.level_2 {
	list-style: none;
}
.col ul,li {
	list-style: none;

}
.dropdown_nav .middle_dropdown_box .col {
	list-style: none;
    padding: 0 11px;
    width: 100%;
}
.dropdown_nav .middle_dropdown_box .flex {
	height: 100%;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -webkit-justify-content: space-between;
    -ms-flex-pack: justify;
    justify-content: space-between;
    padding: 10px;

    color: #444444;
    font-size: 14px;

}


.dropdown_nav .middle_dropdown_box .title_nav {
    color: #655a7b;
    font-size: 16px;
    font-weight: bold;
    position: relative;
    margin-top: 8px;
}
