



.about {
  margin :40px;
  display: flex;
  justify-content: center;
      flex-wrap: wrap;

}
.text_about {
	margin-top: 5px;
	width:100%;
}
.text_about p {
	text-indent: 50px;
	text-align: justify;
}

.all_workers {
  display: flex;
  justify-content: center;
  flex-wrap:wrap;
margin-top: 20px;

}

.first_about_headline{
	margin-top: 0px !important;

}
.myheadline {
  display:flex;
  justify-content: center;
  flex-wrap: wrap;
	text-transform: uppercase;
	text-align: center;
   width:100%;
	 margin-top: 20px;
}

.myheadline h1 {
	color: #4a2c79;
	margin-bottom: 20px;
	font-size: 35px;

	text-transform: uppercase;
}
.myheadline h4 {
	color: #4a2c79;
	font-size: 25px;


	text-transform: uppercase;
}

.workers {

  display:flex;
  justify-content: center;
  width:22%;
  flex-wrap: wrap;
	margin-left: 15px;
	margin-right: 15px;
	height: fit-content;

}
.workers h6 {
	text-align: center;

}

.foto {

  width:200px;
  height:200px;

}
.foto img {
	width: 100%;
	height: 100%;
	border-radius: 50%;
	border: 3px solid  #4a2c79;

}
.title_foto {
	margin-top: 15px;
  display:flex;
  justify-content: center;
 width :100%;



}
.workers_description {
  display:flex;
  justify-content: center;
 width :100%;
  text-align:center;

}

.requisites {
  display:flex;
  justify-content: center;
  width:100%;
	margin-top: 20px;
}


@media screen and (min-width: 290px) and (max-width: 500px) {

	.workers {
		width: 100% !important;
	}


}
